import React from "react";
import { Tabs } from "antd-mobile";
import { TabContent } from "./TabContent";
import { getMemoryCache, setMemoryCache } from "@vxmind/vxhelpers";

export type TabPanelItem = {
    renderTabContent: () => any;
    title: string | any;

}

export type TabPanelProps = {
    sessionKey: string;
    items: TabPanelItem[];
};

export const getTabIndexMemoryKey = key => `TabIndex${key}`;

export function TabPanel(
    {
        items,
        sessionKey
    }: TabPanelProps
) {
    const tabIndexSession = getMemoryCache(getTabIndexMemoryKey(sessionKey));

    const handleTabChange = (tab, index) => {
        setMemoryCache(getTabIndexMemoryKey(sessionKey), index);
    };

    const tabs = items.map(({ title }) => ({ title }));
    return (
        <Tabs
            tabs={tabs}
            initialPage={tabIndexSession}
            onChange={handleTabChange}
        >
            {items.map(
                (item, index) => (
                    <TabContent key={index}>
                        {item.renderTabContent()}
                    </TabContent>
                )
            )}
        </Tabs>
    );
}
