import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { withRouter } from "react-router";
import { TabPanel } from "../components/TabPanel";
import { LeavingListView } from "./LeavingListView";

export type PendingLeaveTabContentProps = {
    history: any;
};

export function PendingLeaveTabContentPure(
    {
        history
    }: PendingLeaveTabContentProps
) {

    const tabs = [
        { title: "待处理", renderTabContent: () => <LeavingListView /> },
        { title: "已处理", renderTabContent: () =>  <LeavingListView /> }
    ];

    return (
        <TabPanel
            sessionKey="PendingLeaveTabContentActiveItem"
            items={tabs}
        />
    );
}

export const PendingLeaveTabContent = compose<PendingLeaveTabContentProps>(
    hot(module),
    withRouter
)(PendingLeaveTabContentPure);
