import React from 'react';
import { ListItem, ListViewDataSource } from "../components/proxyComponents";
import { ListPanel } from "../components/ListPanel";

export function LeavingListView() {
    const messageList = [
        { title: "标题内容", type: "问题整改", direction: "队伍建设" },
        { title: "标题内容", type: "问题整改", direction: "队伍建设" },
    ];

    const dataSource = new ListViewDataSource({
        rowHasChanged: (r1, r2) => r1 !== r2
    });

    const listData = dataSource.cloneWithRows(messageList);

    const renderRow = (item, index) => {
        return (
            <ListItem key={index}>
                {item.title}
            </ListItem>
        );
    };

    return (
        <div>
            <ListPanel
                memoryKey="PolicePublicMessage"
                dataSource={listData}
                renderRow={renderRow}
                style={{height: 300}}
            />
        </div>
    );
}
