import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { withRouter } from "react-router";
import { PageScreen } from "../components/PageScreen";
import { Icon, NavBar } from "antd-mobile";
import { LeavingApplyLeavingPageRoutePath, ModuleRootRoutePath } from "../lazyPages";
import { BottomMenuBar, BottomMenuBarMenuItem } from "../components/BottomMenuBar";
import { PendingLeaveTabContent } from "./PendingLeaveTabContent";

export function HomePage(
    { history }: any
) {

    const handleApplyLeaving = () => {
        history.push(
            LeavingApplyLeavingPageRoutePath
        );
    };

    const tabItems: BottomMenuBarMenuItem[] = [
        {
            title: "待办事宜",
            icon: <i className="zi zi_govDhxk"/>,
            renderContent: () => <PendingLeaveTabContent/>
        },
        {
            title: "流程跟踪",
            icon: <i className="zi zi_govDhxk"/>,
            renderContent: () => "流程跟踪"
        },
        {
            title: "我的申请",
            icon: <i className="zi zi_govDhxk"/>,
            renderContent: () => "我的申请"
        }
    ];

    return (
        <PageScreen hasNavBar>
            <NavBar
                mode="light"
                icon={<Icon type="left"/>}
                onLeftClick={() => history.replace(ModuleRootRoutePath)}
                rightContent={[
                    <a key="applyLeaving" onClick={handleApplyLeaving}>
                        我要公休
                    </a>
                ]}
            >
                请假销假
            </NavBar>


            <BottomMenuBar
                memoryKey="LeaveHomePageBottomActive"
                items={tabItems}
            />
        </PageScreen>
    );
}

export default compose(
    hot(module),
    withRouter
)(HomePage);
