import { Icon, TabBar } from "antd-mobile";
import React, { useState } from "react";
import { compose, getMemoryCache, setMemoryCache } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { withRouter } from "react-router";
import { ModuleRootRoutePath } from "../lazyPages";

export type BottomMenuBarItemRenderOptions = {
    isSelected: boolean;
};

export type BottomMenuBarMenuItem = {
    key?: string | number;
    title: string;
    icon: any;
    renderContent?: (options: BottomMenuBarItemRenderOptions) => any;
    href?: string;
    replaceStateOnPress?: boolean;
};

export type BottomMenuBarProps = {
    hasHome?: boolean;
    memoryKey: string;
    items: BottomMenuBarMenuItem[];
    activeKey?: any;
    history?: any;
};

const genMemoryDataKey = key => `BottomMenuActiveItem${key}`;
const HOME_TAB_KEY = "$$HOME$$";

export function BottomMenuBarPure(
    {
        hasHome = true,
        memoryKey,
        items,
        activeKey,
        history
    }: BottomMenuBarProps
) {

    if (!history || !items.length) {
        return null;
    }

    const menus: BottomMenuBarMenuItem[] = hasHome
        ? [
            {
                key: HOME_TAB_KEY,
                title: "返回",
                icon: <Icon type="left" size="md"/>,
                href: ModuleRootRoutePath,
                replaceStateOnPress: true
            },
            ...items
        ]
        : items;

    const menuList = menus.map((menu, index) => (
        {
            key: menu.key || `${index}`,
            ...menu
        }
    ));

    const activeMemoryDataKey = genMemoryDataKey(memoryKey);

    const activeMemoryKeyData = getMemoryCache(activeMemoryDataKey);

    const [currentActiveKey, setCurrentActiveKey] = useState(
        activeKey
        || activeMemoryKeyData
        || menuList[hasHome ? 1 : 0].key
    );


    const handlePress = (menuItem: BottomMenuBarMenuItem) => {
        if (menuItem.key !== HOME_TAB_KEY) {
            setCurrentActiveKey(menuItem.key);
            setMemoryCache(activeMemoryDataKey, menuItem.key);
        }

        if (menuItem.href && history) {
            if (menuItem.replaceStateOnPress) {
                history.replace(menuItem.href);
            } else {
                history.push(menuItem.href);
            }
        }
    };

    return (
        <TabBar
            unselectedTintColor="#949494"
            tintColor="#33A3F4"
            barTintColor="white"
            prerenderingSiblingsNumber={0}
        >
            {
                menuList.map(
                    (menuItem) => {
                        const { title, key, icon, renderContent } = menuItem;
                        const isSelected = currentActiveKey === key;
                        return (
                            <TabBar.Item
                                title={title}
                                key={key}
                                icon={icon}
                                selectedIcon={icon}
                                selected={isSelected}
                                onPress={() => handlePress(menuItem)}
                            >
                                {renderContent ? renderContent({ isSelected }) : null}
                            </TabBar.Item>
                        );
                    }
                )
            }

        </TabBar>
    );
}

export const BottomMenuBar = compose<BottomMenuBarProps>(
    hot(module),
    withRouter
)(BottomMenuBarPure);
